#header {
  display: grid;
  grid-template-columns: 30% 70%;
  text-align: center;
  align-items: center;
}
.yaui-ctn {
  margin: 1em;
  padding: 1em;
  margin-bottom: 1em;
}
.yaui-form {
  width: 100%;
  padding-top: 0;
}
.yaui-form input {
  margin-top: 5px;
  margin-bottom: 1rem;
  font-size: clamp(1rem, 4vmin, 1.25rem);
}
.yaui-form input:focus {
  font-size: clamp(1rem, 4vmin, 1.3rem);
}

#email-popup {
  top: 0;
  width: clamp(350px, 350px, 100%);
}
#email-popup p {
  text-align: center;
}
/* for screens bigger than 767px */
@media only screen and (min-width: 767px) {


  #header {
    grid-template-columns: 20% 80%;
    justify-content: center;
  }
  #header h2 {
    font-size: clamp(32px, 1.75vmax, 1.75vmax);
  }
  .yaui-ctn {
    width: clamp(600px, 50%, 1000px);
    padding: 1em 6em;
  }
}
